<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <div class="doctor-form">
            <div class="piece">性别</div>
            <div class="info-card">
                <template v-for="(gender, index) in gender_array">
                    <div class="custom-radio" v-on:click="genderHandler(index)" v-bind:class="[ gender.selected ? 'custom-radio-filled' : '' ]">{{ gender.name }}</div>
                </template>
            </div>
            <div class="piece">生日</div>
            <div class="info-card">
                <input v-model="birthday" v-on:change="birthday = $event.target.value" class="input-info birthtime-input" placeholder="请选择生日">
            </div>
            <div class="piece">吸烟史</div>
            <div class="info-card">
                <template v-for="(smoke, index) in smoke_array">
                    <div class="custom-radio" v-bind:class="[ smoke.selected ? 'custom-radio-filled' : '' ]" v-on:click="smokeHandler(index)">{{ smoke.name }}</div>
                </template>
            </div>
            <div class="piece">确诊时间</div>
            <div class="info-card">
                <input v-model="confirmday" class="input-info diagtime-input" placeholder="请选择确诊时间">
            </div>
        </div>
		<div class="ft-bar-next" v-on:click="nextHandler">下一步</div>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>
<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '基本信息',
            nextlink: 'http://www.baidu.com',
            confirmday: '',
            birthday: '',
            gender_array: [
                {
                    name: '男',
                    selected: false
                },
                {
                    name: '女',
                    selected: false
                }
            ],
            smoke_array: [
                {
                    name: '无吸烟史',
                    selected: false
                },
                {
                    name: '5年以下',
                    selected: false
                },
                {
                    name: '5-10年',
                    selected: false
                },
                {
                    name: '10年以上',
                    selected: false
                }
            ]
    	}
    },
    mounted: function(){
        var that = this;
        var birthtimePicker = new MobileSelectDate();
        var diagtimePicker = new MobileSelectDate();
        birthtimePicker.init({
            trigger:'.birthtime-input',
            value:'2011/03/02',
            min:'1990/01/01',
            position:'bottom',
            callback:function(){
                that.birthday = $('.birthtime-input').val().replace(/\//g, '-');
            }
        });
        diagtimePicker.init({
            trigger:'.diagtime-input',
            value:'2011/03/02',
            min:'1990/01/01',
            position:'bottom',
            callback:function(){
                that.confirmday = $('.diagtime-input').val().replace(/\//g, '-');
            }
        });
    },
    methods:{
        genderHandler: function(index){
            var that = this;
            for(var i=0;i<that.gender_array.length;i++){
                that.gender_array[i].selected = false;
            }
            that.gender_array[index].selected = true;
        },
        smokeHandler: function(index){
            var that = this;
            for(var i=0;i<that.smoke_array.length;i++){
                that.smoke_array[i].selected = false;
            }
            that.smoke_array[index].selected = true;
        },
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 1500);
    	},
        nextHandler: function(){
            var that = this;
            var data = that.getFormData();
            if(data.sex == '' || data.sex == undefined){
                that.toast_text = '请选择您的性别！';
                that.showToast();
                return;
            }
            if(data.birthday == '' || data.birthday == undefined){
                that.toast_text = '请填写您的生日！';
                that.showToast();
                return;
            }
            if(data.smoke_history == '' || data.smoke_history == undefined){
                that.toast_text = '请选择吸烟史！';
                that.showToast();
                return;
            }
            if(data.diagnosis_time == '' || data.diagnosis_time == undefined){
                that.toast_text = '请填写确诊日期！';
                that.showToast();
                return;
            }
            store.user_info(data).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.pathologyinfo;
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
        },
        getFormData: function(){
            //dealData处理成form数据，data对象key对应后端接口
            var that = this;
            var data = {};
            data.role = 0;
            data.hospital = '';
            data.department = '';
            for(var i=0;i<that.gender_array.length;i++){
                if(that.gender_array[i].selected){
                    data.sex = i == 1 ? 'F' : 'M';
                }
            }
            for(var i=0;i<that.smoke_array.length;i++){
                if(that.smoke_array[i].selected){
                    data.smoke_history = i;
                }
            }
            data.birthday = that.birthday;
            data.diagnosis_time = that.confirmday;
            return data;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/date-picker-bottom.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
</style>



