import axios from 'axios';

const store = {};
export default  store;

var req_instance = axios.create({
    baseURL: 'http://s2.chilunyc.com:8012/',
    timeout: 1000,
    headers: {'content-type': 'application/json'}
});

var env = location.href.indexOf('localhost') > 0 ? 0: 1;   //0为本地，1为线上

store.site_links = (function(){
    switch(env){
        case 0:
            return {
                index: 'index.html',                     //首页
                articledetail: 'articledetail.html',     //文章详情
                forum: 'forum.html',                     //论坛
                forumdetail: 'forumdetail.html',         //论坛详情
                signin: 'signin.html',                   //登录
                signup: 'signup.html',                   //注册
                signinfo: 'signinfo.html',               //补充信息
                usertype: 'usertype.html',               //用户类型
                doctorinfo: 'doctorinfo.html',           //医生信息
                baseinfo: 'baseinfo.html',               //基本信息
                pathologyinfo: 'pathologyinfo.html',     //病理信息
                tomour: 'tomour.html',                   //肿瘤指标
                liver_function: 'liverfunction.html',    //肝功能
                renal_function: 'renalfunction.html',    //肾功能
                heart: 'heart.html',                     //心脏
                immune: 'immune.html'                    //免疫功能
            }
        break;
        case 1:
            return {
                index: '/home',
                articledetail: '/article/detail/',
                forum: '/forum',
                forumdetail: '/forum/post/detail/{id}',
                signin: '/login',
                signup: '/register',
                signinfo: '/user/supplementary_information',
                usertype: '/user/role',
                doctorinfo: '/user/basic_information/doctor',
                baseinfo: '/user/basic_information/user',
                pathologyinfo: '/user/pathological_information',
                tomour: '/user/tumor_function_index/first_add',
                liver_function: '/user/liver_function_index/first_add',
                renal_function: '/user/renal_function_index/first_add',
                heart: '/user/heart_function_index/first_add',
                immune: '/user/immunity_function_index/first_add'
            }
        break;
    }
})();

