store.init_index = function(){
    return req_instance.get('home/init?article_category_id=1&page=1&size=6');
}

store.init_article = function(id){
    return req_instance.get('article/detail/init/'+id);
}

store.init_forum = function(){
    return req_instance.get('forum/init');
}

store.article_comment = function(article_id, comment_id, comment_res_id, comment){
    return req_instance.post('', {
        id: article_id,
        comment_id: comment_id,
        comment_response_id: comment_res_id,
        comment: comment
    });
}

store.thumb_up = function(article_id){
    return req_instance.get('forum/post/cheer/'+article_id);
}

store.login = function(mobile, password){
    return req_instance.post('login', {
        mobile: mobile,
        password: password
    });
}

store.authcode = function(action, mobile){
    return req_instance.get('verification_sms/'+action+'/'+mobile);
}

store.quicklogin = function(mobile, authcode){
    return req_instance.post('quick_login', {
        mobile: mobile,
        verification_code: authcode
    });
}

store.register = function(nickname, mobile, authcode, password){
    return req_instance.post('register', {
        nickname: nickname,
        mobile: mobile,
        verification_code: authcode,
        password: password
    });
}

store.supplementary_info = function(nickname, password){
    return req_instance.post({
        nickname: nickname,
        password: password
    });
}

