<template>
	<div class="header">
		<a href="javascript:history.back()" class="header-arrow-left"></a>
		<div class="header-title">{{ title }}</div>
	</div>
</template>

<script>
	export default {
		props: ['title']
	}
</script>

<style>
	.header{
	    width: 100%;
	    height: 45px;
	    background: #49ced4;
	    padding: 10px 15px;
	    display: flex;
	    align-items: center;
	}
	.header-arrow-left{
	    width: 24px;
	    height: 24px;
	    background-image: url('../../assets/images/titlebar_back@2x.png');
	    background-size: 100%;
	    background-repeat: no-repeat;
	    position: absolute;
	}
	.header-title{
	    color: #fff;
	    margin: 0px auto;
	}
</style>


