<template>
	<div class="wxl-toast">
		<div class="wxl-toast-text">{{ msg }}</div>
	</div>
</template>

<script>
	export default {
		props: ['msg'],
		data: function(){
			return {

			}
		}
	}
</script>

<style type="text/css">
	.wxl-toast{
		min-width: 60%;
		padding: 10px 20px;
		background: #000;
		opacity: 0.6;
		color: #fff;
		position: fixed;
		top: 50%;
		left: 50%;
		border: 1px solid #000;
		border-radius: 3px;
		transform: translate(-50%, -50%);
		text-align: center;
	}
</style>


